.container {
  position: absolute;
  top: 60px;
  z-index: 1;
  width: 100%;
  height: 400px;
  padding: 10px 0 20px 30px;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  border-radius: 18px;
  box-shadow: 0px 66px 60px rgba(62, 63, 99,  0.05);
  background: white;
  overflow: scroll;
  overflow-x: hidden;
  box-sizing: border-box;
  scroll-behavior: smooth;

  .year {
    height: 30px;
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 1px 1px rgba(0, 14, 51, 5%);
    border-radius: 6px;
    font-size: 24px;
    line-height: 100%;
    color: #141414;
    cursor: pointer;

    &:hover {
      box-shadow: 0px 1px 7px rgba(24, 39, 75, 28%);
    }
  }

  .activeYear {
    background: rgb(161, 73, 219);
    color: white;
  }
}