.Graph{position:relative;min-width:354px;height:270px;padding:15px;display:flex;flex-direction:column;gap:15px;background:#fff;box-shadow:0px 4px 60px rgba(0,0,0,.1);border-radius:16px}.Graph .noData{position:absolute;top:50%;left:50%;font-weight:600;font-size:14px;line-height:25px;text-align:center;color:#c1c5d6;transform:translate(-50%, -50%)}.Graph .title{font-size:18px;line-height:24px}.Graph .title::after{content:" (Топ 4)";font-size:16px}.Graph .percents{position:absolute;left:16px;right:15px;bottom:15px;display:flex;justify-content:space-between}.Graph .percents .percent{width:24px;font-size:10px;line-height:20px;opacity:.8}